.hwTitle {
  font-family: "Montserrat", sans-serif;
  font-size: 22px;
  font-weight: 600;
  margin: 50px 0 50px;
  position: relative;
}
.hwTitle:after {
  content: "";
  border-top: 2px solid #d9d9d9;
  width: 100%;
  position: absolute;
  top: 35px;
  left: 0;
}
.content {
  display: flex;
  justify-content: revert;
}

